const getBodyStr = (value) => {
    if (typeof value === "object") {
        return JSON.stringify(value);
    }
    else if (typeof value === "string") {
        return value;
    }
    else {
        return "";
    }
};
export const generateHttpDump = (transaction) => {
    return `
#############################
# Requested from ${transaction.start} to ${transaction.end}
# IP: ${transaction.request.ip}
${transaction.request.method} ${transaction.request.originalUrl}
${Object.keys(transaction.request.headers).map((key) => {
        return `${key}: ${transaction.request.headers[key]}\n`;
    }).join("")}

${getBodyStr(transaction.request.body)}###### RESPONSE
${transaction.response.code}
${getBodyStr(transaction.response.body)}
`;
};
export const dumpEverything = async () => {
    return (await Promise.all((await (await fetch(`/_debug/api/`)).json()).map(async (entry) => {
        return generateHttpDump(await (await fetch(`/_debug/api/${entry.uuid}`)).json());
    }))).join("\n");
};
export const downloadFile = (filename, text) => {
    var element = document.createElement('a');
    element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
    element.setAttribute('download', filename);
    element.style.display = 'none';
    document.body.appendChild(element);
    element.click();
    document.body.removeChild(element);
};




