import React, { useState } from 'react';
import styled from 'styled-components';
const TabsContainer = styled.div `
  display: flex;
  flex-direction: column;
`;
const TabWrapper = styled.div `
  display: flex;
`;
const TabButton = styled.div `
  min-width: 2em;
  font-size: 1.2em;
  padding: 1em;
  padding-right: 1.5em;

  cursor: pointer;

  :hover {
    background-color: rgba(0,0,0,0.2);
  }
`;
const ChildContainer = styled.div `

`;
export const Tabs = ({ children, defaultTab }) => {
    const [currentTab, setCurrentTab] = useState(defaultTab);
    return (React.createElement(TabsContainer, null,
        React.createElement(TabWrapper, null, children.map((child) => {
            const ch = child; //Forgive me lord for i have sinned
            if (ch === undefined || ch === null) {
                return null;
            }
            return React.createElement(TabButton, { style: ch.props.title == currentTab ? { backgroundColor: "rgba(0,0,0,0.2)" } : {}, key: ch.props.title, onClick: () => {
                    setCurrentTab(ch.props.title);
                } }, ch.props.title);
        })),
        React.createElement(ChildContainer, null, children.filter((child) => {
            const ch = child;
            if (ch === undefined || ch === null) {
                return null;
            }
            return ch.props.title == currentTab;
        }))));
};
export const Tab = ({ title, children }) => {
    return (React.createElement("div", null, children));
};



